/**
 * 
 * @param {object} dbName 
 * @param {string} storeName 
 * @param {string} version 
 * @return {object} 
 */
export function openDB (dbName, version = '1') {
  return new Promise((resolve, reject) => {
    //  
    var indexedDB =
      window.indexedDB ||
      window.mozIndexedDB ||
      window.webkitIndexedDB ||
      window.msIndexedDB;
    let db;
    // 
    const request = indexedDB.open(dbName, version);
    // 
    request.onsuccess = function (event) {
      db = event.target.result; // 
      console.log('');
      resolve(db);
    };
    // 
    request.onerror = function (event) {
      console.log('');
    };
    // 
    request.onupgradeneeded = function (event) {
      // 
      db = event.target.result; // 
      var objectStore;
      // 
      objectStore = db.createObjectStore('menuList', {
        keyPath: 'id' // 
        // autoIncrement: true // 
      });
      // 
      objectStore.createIndex('id', 'id', { unique: false });
      objectStore.createIndex('code', 'code', { unique: false });
      objectStore.createIndex('type', 'type', { unique: false });
      objectStore.createIndex('name', 'name', { unique: false });
      objectStore.createIndex('permission', 'permission', { unique: false });
      objectStore.createIndex('path', 'path', { unique: false });
      objectStore.createIndex('comp', 'comp', { unique: false });
      objectStore.createIndex('parentId', 'parentId', { unique: false });
      objectStore.createIndex('parentIds', 'parentIds', { unique: false });
    };
  });
}

/**
 * 
 * @param {object} db 
 * @param {string} storeName 
 * @param {string} data 
 */
export function addData (db, storeName, data) {
  return new Promise((resolve, reject) => {
    var request = db
      .transaction(storeName, 'readwrite') //  """"
      .objectStore(storeName) // 
      .add(data);
    request.onsuccess = function (event) {
      console.log('');
      resolve({ code: 200, msg: '' });
    };

    request.onerror = function (event) {
      console.log('');
      resolve({ code: 500, msg: '' });
    };
  });
}

// 
export function cursorGetData (db, storeName) {
  return new Promise((resolve, reject) => {
    const list = [];
    var store = db
      .transaction(storeName, 'readwrite') // 
      .objectStore(storeName); // 
    var request = store.openCursor(); // 
    // 
    request.onsuccess = (e) => {
      var cursor = e.target.result;
      if (cursor) {
        // 
        list.push(cursor.value);
        cursor.continue(); // 
      } else {
        resolve({ code: 200, list });
      }
    };
    request.onerror = (e) => {
      // eslint-disable-next-line prefer-promise-reject-errors
      reject({ code: 500, msg: '' });
    };
  });
}

/**
 * 
 * @param {object} db 
 * @param {string} storeName 
 * @param {string} key 
 */
export function getDataByKey (db, storeName, key) {
  return new Promise((resolve, reject) => {
    var transaction = db.transaction([storeName]); // 
    var objectStore = transaction.objectStore(storeName); // 
    var request = objectStore.get(key); // 

    request.onerror = function (event) {
      console.log('');
    };

    request.onsuccess = function (event) {
      resolve(request.result);
    };
  });
}

const promise = openDB('yian', '1');
// const that = this;

export let instanceDB;
promise.then(function (value) {
  instanceDB = value;
}, function (error) {
  console.log('error', error);
});

/**
 * 
 * @param {object} db 
 * @param {string} storeName 
 * @param {object} data 
 */
export function updateDB (db, storeName, data) {
  return new Promise((resolve, reject) => {
    var request = db
      .transaction([storeName], 'readwrite') // 
      .objectStore(storeName) // 
      .put(data);

    request.onsuccess = () => {
      console.log('');
      resolve(200);
    };

    request.onerror = () => {
      console.log('');
      // eslint-disable-next-line prefer-promise-reject-errors
      reject(500);
    };
  });
}
