import Mock from 'mockjs';
import { PRIVATEKEY, uStr, pStr } from '@/RSA';
import JSEncrypt from 'jsencrypt/bin/jsencrypt';
// import login from './json/login.json' //  data
const Random = Mock.Random;
// url 
Mock.mock('/mock/login', 'post', (data) => {
  const body = JSON.parse(data.body);
  const { password, username } = body;
  let params = {};
  if (decrypt(username) === decrypt(uStr) && decrypt(password) === decrypt(pStr)) {
    params = {
      code: 200,
      data: {
        name: Random.cname(),
        userId: Random.guid()
      },
      msg: ''
    };
  } else {
    params = {
      code: 500,
      data: {},
      msg: ''
    };
  }
  return params;
});

function decrypt (txt) {
  const decryptor = new JSEncrypt(); // JSEncrypt
  decryptor.setPrivateKey(PRIVATEKEY); // 
  return decryptor.decrypt(txt); // 
}
